@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css');

:root {
  --main-bg: #fff;
  --main-red: #8B2D25;
  --main-text-dark: #8B2D25;
  --main-text-light: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  width: 100%;
  font-family: sans-serif;
  background-color: var(--main-bg);
  color: var(--main-text-light);
}

body, .app-wrapper, .app-container {
  background: var(--main-bg);
  color: var(--main-text-dark);
  height: 100vh;
}

.app-header {
  background: var(--main-red);
  color: var(--main-text-light);
  padding: 1rem 2rem;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  box-shadow: none;
  justify-content: center;
}

.header-inner {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-logo {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--main-text-light);
  font-family: 'Pacifico', cursive;
  letter-spacing: 2px;
  margin: 0 auto;
}

.settings-btn {
  position: absolute;
  right: 2rem;
  top: 1.2rem;
  background: none;
  border: none;
  color: var(--main-text-light);
  font-size: 1.5rem;
  cursor: pointer;
}

.settings-btn:hover {
  color: var(--main-red);
  background: var(--main-text-light);
}

.app-wrapper {
  background: var(--main-bg);
  color: var(--main-text-dark);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.app-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.5rem;
  background: var(--main-bg);
  height: calc(100vh - 64px);
  overflow: hidden;
}

.map-container {
  flex: 1 1 0;
  min-height: 0;
  background: var(--main-bg);
  border-radius: 12px;
  overflow: hidden;
  height: 0;
}

.travel-info, .start-btn {
  flex-shrink: 0;
}

.travel-info {
  background: var(--main-bg);
  border-radius: 12px;
  padding: 1rem;
  color: var(--main-text-dark);
}

.travel-info-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.section-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--main-red);
  margin-bottom: 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.section-title i {
  font-size: 1.2rem;
}

.travel-info-section {
  background: var(--main-bg);
  border-radius: 8px;
  padding: 1.5rem;
}

.travel-info-section .section-title {
  margin-bottom: 1rem;
  color: var(--main-red);
  font-size: 1.1rem;
  font-weight: 600;
}

.route-info-item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  color: var(--main-text-dark);
  font-size: 0.95rem;
}

.route-info-item i {
  color: var(--main-red);
  font-size: 1.2rem;
  width: 1.5rem;
  text-align: center;
}

.route-info-item span {
  color: var(--main-text-dark);
  font-size: 0.9rem;
}

.route-info {
  margin-top: 1rem;
}

.start-btn {
  padding: 1rem;
  background: var(--main-red);
  color: var(--main-text-light);
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
}

.start-btn:hover {
  background: var(--main-text-light);
  color: var(--main-red);
}

.settings-popup,
.detail-popup {
  background: var(--main-bg);
  color: var(--main-text-dark);
  border-radius: 16px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
}

.settings-popup-header {
  padding: 1.2rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: none;
}

.settings-popup-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: var(--main-red);
  font-weight: 600;
  text-align: left;
}

.settings-popup-header .close-btn {
  margin-left: auto;
}

.close-btn {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  color: var(--main-red);
  font-size: 1.2rem;
  cursor: pointer;
  border-radius: 50%;
}

.close-btn:hover {
  background: var(--main-red);
  color: var(--main-text-light);
}

.save-btn {
  padding: 0.8rem 2rem;
  background: var(--main-red);
  color: var(--main-text-light);
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
}

.save-btn:hover {
  background: var(--main-text-light);
  color: var(--main-red);
}

.settings-popup-content {
  padding: 1.5rem;
  flex: 1;
  overflow-y: auto;
}

.settings-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 0;
}

.location-section,
.transport-section,
.time-section {
  background: var(--main-bg);
  border: none;
  border-radius: 12px;
  padding: 1.5rem;
  position: relative;
}

.location-section::after,
.transport-section::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: var(--main-red);
  opacity: 0.2;
}

.location-input-wrapper {
  display: flex;
  gap: 0.8rem;
}

.location-input {
  flex: 1;
  padding: 0.8rem 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 0.95rem;
  background: var(--main-bg);
  color: var(--main-text-dark);
}

.location-input:focus {
  outline: none;
  border-color: var(--main-red);
}

.location-btn {
  width: 42px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--main-red);
  color: var(--main-text-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.location-btn:hover {
  background: var(--main-text-light);
  color: var(--main-red);
}

.transport-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.transport-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  padding: 1.2rem;
  border: 2px solid var(--main-red);
  border-radius: 12px;
  background: var(--main-bg);
  cursor: pointer;
  transition: all 0.2s ease;
}

.transport-btn:hover {
  background: var(--main-bg);
  border-color: var(--main-red);
}

.transport-btn-selected {
  background: var(--main-red) !important;
  border-color: var(--main-red);
}

.transport-btn-selected i,
.transport-btn-selected span {
  color: var(--main-text-light) !important;
}

.transport-btn i {
  font-size: 1.8rem;
  color: var(--main-red);
}

.transport-btn span {
  font-size: 0.95rem;
  color: var(--main-text-dark);
  font-weight: 500;
}

.time-range-slider {
  width: 100%;
  height: 4px;
  -webkit-appearance: none;
  background: #ccc;
  border-radius: 2px;
  outline: none;
  margin: 1rem 0;
}

.time-range-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background: var(--main-red);
  border-radius: 50%;
  cursor: pointer;
}

.time-range-slider::-webkit-slider-thumb:hover {
  background: var(--main-text-light);
}

.time-range-labels {
  display: flex;
  justify-content: space-between;
  font-size: 0.85rem;
  color: var(--main-red);
}

.current-time {
  color: var(--main-red);
  font-weight: 500;
}

.detail-btn {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--main-bg);
  color: var(--main-red);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  margin-left: 1rem;
}

.detail-btn:hover {
  background: var(--main-text-light);
  color: var(--main-red);
}

.detail-btn i {
  font-size: 1.2rem;
}

.detail-popup-overlay,
.settings-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(26, 24, 23, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.detail-popup,
.settings-popup {
  background: var(--main-bg);
  color: var(--main-text-dark);
  border-radius: 16px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
}

.detail-popup-header {
  padding: 1.2rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: none;
}

.detail-popup-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: var(--main-red);
  font-weight: 600;
  text-align: left;
}

.detail-popup-header .close-btn {
  margin-left: auto;
}

.detail-popup-content {
  padding: 1.5rem;
  flex: 1;
  overflow-y: auto;
}

.detail-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.detail-section {
  background: var(--main-bg);
  border: 1px solid var(--main-red);
  border-radius: 12px;
  padding: 1.5rem;
}

.detail-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--main-red);
  margin-bottom: 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.detail-title i {
  font-size: 1.2rem;
}

.detail-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.detail-info-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: var(--main-text-dark);
  font-size: 0.95rem;
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(139, 45, 37, 0.1);
}

.detail-info-item:last-child {
  border-bottom: none;
}

.detail-info-item i {
  color: var(--main-red);
  font-size: 1.2rem;
  width: 1.5rem;
  text-align: center;
}

.no-route-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
  text-align: center;
  color: var(--main-red);
}

.no-route-message i {
  font-size: 3rem;
  color: var(--main-red);
}

.no-route-message p {
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .app-header {
    padding: 1rem;
  }
  .header-logo {
    font-size: 1.5rem;
  }
  .app-main {
    padding: 0.5rem;
    gap: 0.5rem;
  }
  .settings-popup,
  .detail-popup {
    width: 95%;
    max-height: 95vh;
  }
  .settings-popup-header,
  .detail-popup-header {
    padding: 1rem;
  }
  .settings-popup-content,
  .detail-popup-content {
    padding: 1rem;
  }
  .settings-grid,
  .detail-grid {
    gap: 1rem;
}
  .location-section,
  .transport-section,
  .time-section,
  .detail-section {
    padding: 1rem;
  }
  .travel-info {
    padding: 0.8rem;
  }
  .travel-info-section {
    padding: 1rem;
  }
  .no-route-message {
    padding: 1.5rem;
  }
  .no-route-message i {
    font-size: 2.5rem;
  }
  .detail-info-item {
    font-size: 0.9rem;
    padding: 0.4rem 0;
}
  .transport-btn {
    padding: 1rem;
  }
  .transport-btn i {
    font-size: 1.5rem;
  }
  .detail-popup-header {
    padding: 1rem;
  }
  .detail-popup-header h2 {
    font-size: 1.3rem;
}
  .settings-popup-header {
    padding: 1rem;
  }
  .settings-popup-header h2 {
    font-size: 1.3rem;
}
  .settings-popup-content {
    padding: 1rem;
  }
  .settings-grid {
    gap: 1rem;
  }
  .location-section,
  .transport-section,
  .time-section {
    padding: 1rem;
  }
}

::-webkit-scrollbar {
  display: none;
}
